table, th, td {    
    border: 3px solid black;  
    margin-left: auto;  
    margin-right: auto;
    border-collapse: collapse;    
    width: 200px;  
    text-align: center;  
    font-size: 20px;  
    max-width: 60%;
    border:#100505;
    max-width: 90%;
    
  }  
  .center {
    width: 50%;
    margin: auto;
    padding: 10px;
    text-align: center;
  }
  .container-4{
    width: fit-content;
    margin: auto;
    display: flex;
    flex-wrap: wrap;
    border:#1b1616;
  }
  .container {
    max-width:1500px; /* Adjust as needed */
    margin: 1px solid black; /* Center the container horizontally */
    padding: 20px; /* Add padding to the container */
    background-color: #a5c39c; /* Background color */
    border-radius: 8px; /* Add rounded corners */
    box-shadow: 0 0 10px rgba(7, 29, 229, 0.1); /* Add a subtle shadow */
    
    
  }
  
  .container h2 {
    color: #320b02; /* Heading color */
  }
  
  .container hr {
    border-top: 1px solid #6b1f65; /* Horizontal rule color */
    margin: 20px 0; /* Add spacing above and below the horizontal rule */
    
  }